import uniIcon from "@/components/uni/uni-icon/uni-icon.vue"
import uniPopup from "@/components/uni/uni-popup/uni-popup.vue"
import robbyImageUpload from '@/components/plan-image-upload/up_img'
import {
	Api_url
} from '@/common/config'
export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		const currentDate_end = this.getDate_end({
			format: true
		})
		return {
			status: [{
					value: '0',
					name: '一次'
				},
				{
					value: '1',
					name: '无限制'
				},
			],
			current: 0,
			coupon_form:{
				status:'',
				full:'',
				reduce:'',
				name:'',
				stock:'',
				sm:'',
				goods_ids:'0',
				star_time:'',
				end_time:'',
				stock_type:'',
				
			},
			product_cate: '',
			set: 0,
			category_id: 1,
			date: currentDate,
			date_end: currentDate_end,
			time: '12:01',
			threshold: 1,
			show_cate_list: false,
			show_mail_list: false,
			category_name: '固定时间',
			category_list: [{
					category_id: '1',
					category_name: '固定时间'
				},
				{
					category_id: '2',
					category_name: '领卷当日开始计算有效期'
				}
			],
			id: 0,
			mail: 1,
			mail_list: [{
					id: '1',
					name: '全部商品'
				},
				{
					id: '2',
					name: '指定商品可用'
				}
			],
			group: '未分组',
		};
	},
	components: {
		uniIcon,
		uniPopup,
		robbyImageUpload
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	watch: {},
	onLoad(options) {
		this._request()
		this.id = options.id
	},
	onShow() {},
	methods: {
		//----------------------------------------------------------使用设置
		setting() {
			this.set = 1
		},
		//-------------------------------------------------------------选择使用次数
		radioChange: function(evt) {
			for (let i = 0; i < this.status.length; i++) {
				if (this.status[i].value === evt.target.value) {
					this.current = i;
					console.log(this.status[i].value)
					this.coupon_form.status = this.status[i].value
					break;
				}
			}
		},
		//-----------------------------------------------------------优惠券开始日期
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let month_end = date.getMonth() + 2;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		//-----------------------------------------------------优惠券结束时间
		getDate_end(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 2;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 20;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		},
		bindDateChange: function(e) {
			this.date = e.target.value
			console.log(this.date)
		},
		bindDateChange_end: function(e) {
			this.date_end = e.target.value
		},
		//-------------------------------------------------改变switch状态
		switch1Change() {
			if (this.threshold == 1) {
				this.threshold = 0
				this.coupon_form.full = 0
			} else {
				this.threshold = 1
			}
		},
		

		//---------------------------------------------------------------提交
		sub() {
			this.coupon_form.goods_ids = [1,2,3]
			this.coupon_form.star_time = this.date
			this.coupon_form.end_time = this.date_end
			this.coupon_form.stock_type = '1'
			console.log(this.coupon_form)
			this.$api.http.post('shop_cms/add_vip_coupon',{
				stock_type:'1',
				full:this.coupon_form.full,
				name:this.coupon_form.name,
				reduce:this.coupon_form.reduce,
				status:'2',
			}).then(res=>{
				this.$api.msg('添加成功')
				uni.redirectTo({
				    url: '../couponlist/couponlist'
				});
			})
			this.coupon_form = {}
		},
		
		compressImg(e) {
			console.log(e)
		},
		changeIndicatorDots(e) {
			this.isYasuo = !this.isYasuo
		},
		
		buy(type) {
			this.$api.http.post('user/pay_vip', {
				type
			}).then(res => {
				this.$api.msg('购买成功')
				setTimeout(() => {
					uni.switchTab({
						url: '/pages/user/user'
					})
				}, 1500)
			})
		}
	}
}
